import React from 'react'
import '../../../style/App.css';

let ListParty = React.memo(props => {
    let SendAngOff=(i)=>{
        props.itsRename('PROFILE', {list:i.ListPeople, id:i._id})
        let style=document.getElementById("list")
            style.style.pointerEvents='none';
            style.style.color='gray';
            setTimeout(()=>{
                style.style.pointerEvents=null
                style.style.color='black';
            },2000)
        console.log(style)

    }

    let DialogItem = props.party.map((i) => {return (
        <div className={"List-item-Party-Friend"} onClick={() => {SendAngOff(i)}}>
            <div>
                <img id={"ava-min"} src={i.AvaParty} alt={"Pic"} />
            </div>
            <div>
               <div>{i.NameParty.map(e=>e).join(' ')||'Name Party'} , {i.NumberOfPerson}</div>
            </div>
            {
              i.admin.filter(r=>r===props.user).length!==0
              ? props.setting && <input type={'checkbox'} onClick={()=>{props.itsRename("SELECTED-PARTY", i._id)}} />
              : props.setting && <button onClick={()=>{props.itsRename("LEAVING-THE-PARTY", i._id)}}>Leave</button>
            }
        </div>
    )});
    return (<div id={'list'}>
            {DialogItem}
        </div>
    )
});

export default ListParty;



